	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/experiments-with-html5-geolocation/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"http://www.psyked.co.uk/geolocation/index.html\"><img src=\"http://uploads.psyked.co.uk/2011/04/geolocation.png\" title=\"Geolocation screenshot\"></a></p>\n<p>I've been exploring the <a href=\"http://www.w3.org/TR/geolocation-API/\" title=\"Open the W3C specification for Geolocation\">HTML5 Geolocation specification</a> recently, and it's both fascinating and a little scary how accurate your browsers' Geolocation capabilities have become, especially if you're working on a machine with Wifi capabilities.</p>\n<p>Inspired by this, I thought it would be cool to come up with some elaborate UAV interface mockup in HTML5 (so that it would work on iOS devices) and throw some cool stats in there.</p>\n<p>As it turns out, I get bored too quickly and never finished these experiments, but my <a href=\"http://www.psyked.co.uk/geolocation/\">initial prototypes are available here</a>. Sans explanation and documentation, of course.</p>","frontmatter":{"title":"Experiments with HTML5 Geolocation","date":"April 12, 2011","tags":["html5","geolocation"]}}},"pathContext":{}}

